<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="news_title">
                    <el-row type='flex' justify="space-around">
                        <el-col :span='5'>
                            <div class='news_t_c'>
                                <el-row>
                                    <el-col :span='7'>
                                        <span>新闻标题：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-input v-model='name' @input='select'></el-input>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='6'>
                            <div class='news_t_c'>
                                <el-row>
                                    <el-col :span='7'>
                                        <span>发布时间：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-date-picker style='width:100%;font-size:12px;' v-model="time" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                                        </el-date-picker>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class='news_t_c'>
                                <el-row>
                                    <el-col :span='7'>
                                        <span>版块：</span>
                                    </el-col>
                                    <el-col :span='17'>
                                        <el-select v-model="ban_k" placeholder="请选择" style='width:100%;'>
                                            <el-option v-for="(item,index) in options" :key="index" :label="item.press_name" :value="item.id">
                                            </el-option>
                                        </el-select>
                                    </el-col>
                                    <!-- <el-col :span='7'>
										<span>作者：</span>
									</el-col>
									<el-col :span='17'>
										<el-input v-model='username'></el-input>
									</el-col> -->
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="news_t_c">
                                <el-button type="primary" @click='select' icon="el-icon-search">
                                    查询
                                </el-button>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <div class="news_mg">
                    <div class="news_mg_t">
                        <span @click='on' :class='num==0?"active":""' class="ss">已发布 ({{list.length}})</span>
                        <span @click='off' :class='num==1?"active":""'>未发布 ({{lists.length}})</span>
                        <el-button type="primary" @click='add' icon='el-icon-plus'>添加</el-button>
                    </div>
                    <!-- 已发布 -->
                    <div class="news_mg_c" v-if='num==0'>
                        <el-row v-for='(i,index) in list.slice((currentPage-1)*pagesize,currentPage*pagesize)' :key='index'  @click.native='news_d(i,i.status)'>
                            <el-col :span='2'>
                                <div class="news_mg_c_img">
                                    <img :src="i.photo" alt="">
                                </div>
                            </el-col>
                            <el-col :span='22'>
                                <el-row>
                                    <el-col :span='18'>
                                        <div class='tit'>
                                            <span>{{i.title}}</span>
                                        </div>
                                        <div class='tit_c'>
                                            <span>{{i.userName}}</span>
                                            <span>{{i.createTime}}</span>
                                        </div>
                                    </el-col>
                                    <el-col :span='6'>
                                        <el-row>
                                            <el-col :span='12'>
                                                <div class="tit_r">
                                                    <!-- {{i.status==1?'发布':'未发布'}} -->
                                                    <!-- <span @click.stop='del(i.id)' style='cursor:pointer;'>删除</span> -->
                                                    <span style='cursor:pointer;'>{{i.pressName}}</span>
                                                </div>
                                            </el-col>
                                            <el-col :span='12'>
                                                <div class="tit_r tit_rs">
                                                    <span @click.stop='ret(i.id)' style='color:#0000FF;'><i class="el-icon-refresh"></i> 撤回</span>
                                                </div>
                                            </el-col>
                                        </el-row>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                        <el-row class='n'>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="list.length">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                    <!-- 未发布 -->
                    <div class="news_mg_c" v-if='num==1'>
                        <el-row v-for='(i,index) in lists.slice((currentPage-1)*pagesize,currentPage*pagesize)' :key='index' @click.native='news_d(i,i.status)'>
                            <el-col :span='2'>
                                <div class="news_mg_c_img">
                                    <img :src="i.photo" alt="">
                                </div>
                            </el-col>
                            <el-col :span='22'>
                                <el-row>
                                    <el-col :span='18'>
                                        <div class='tit'>
                                            <span>{{i.title}}</span>
                                        </div>
                                        <div class='tit_c'>
                                            <span>{{i.userName}}</span>
                                            <span>{{i.createTime}}</span>
                                        </div>
                                    </el-col>
                                    <el-col :span='6'>
                                        <el-row>
                                            <el-col :span='12'>
                                                <div class="tit_r">
                                                    <!-- {{i.status==1?'发布':'未发布'}} -->
                                                    <!-- <span @click.stop='del(i.id)' style='cursor:pointer;'>删除</span> -->
                                                    <span style='cursor:pointer;'>{{i.pressName}}</span>
                                                </div>
                                            </el-col>
                                            <el-col :span='12'>
                                                <div class="tit_r tit_rs">
                                                    <!-- <span>浏览 {{count}}</span> -->
                                                    <span @click.stop='del(i.id)' style='cursor:pointer;color: #0000FF;'><i class="el-icon-delete"></i> 删除</span>
                                                </div>
                                            </el-col>
                                        </el-row>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                        <el-row class='n'>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="lists.length">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '新闻管理',
            news_list: [],
            num: 0,
            currentPage: 1,
            pagesize: 10,
            total: 0,
            name: '',
            time: '',
            username: '',
            ban_k: '',
            options: [],
            count: 0
        }
    },
    methods: {
        // 查询
        formatDate(date) {
            if (typeof date == "string") {
                return this.formatDate2(date);
            }
            var y = date.getFullYear();
            var M = date.getMonth() + 1;
            M = M < 10 ? "0" + M : M;
            var d = date.getDate();
            d = d < 10 ? "0" + d : d;
            var h = date.getHours();
            h = h < 10 ? "0" + h : h;
            var m = date.getMinutes();
            m = m < 10 ? "0" + m : m;
            var s = date.getSeconds();
            s = s < 10 ? "0" + s : s;
            return y + "-" + M + "-" + d + " " + h + "-" + m + "-" + s;
        },
        del(id) {
            this.$confirm('是否删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/pressCenter/deletePressCenterByPrimaryKey', this.$qs.stringify({
                        id: id
                    })).then((res) => {
                        this.all()
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
            console.log(id)

        },
        select() {
            var a;
            var b;
            if (this.time == '') {
                a = '';
                b = '';
            } else {
                a = this.formatDate(this.time[0])
                b = this.formatDate(this.time[1])
            }
            this.$axios.post('/tomlive/pressCenter/selectPressCenterByCondition', this.$qs.stringify({
                title: this.name,
                // userName:this.username,
                beginTime: a,
                endTime: b,
                pressTypeId: this.ban_k
            })).then((res) => {
                this.news_list = res.data.info.reverse()
            })
        },
        /*详情页*/
        news_d(val, status) {
            if (status == 0) {
                window.sessionStorage.setItem('news_d', JSON.stringify(val))
                this.$router.push('/home/news_edit')
            } else {
                window.sessionStorage.setItem('news_d', JSON.stringify(val))
                this.$router.push('/home/news_d');
            }
        },
        on() {
            this.num = 0
        },
        off() {
            this.num = 1
        },
        add() {
            this.$router.push('/home/add_news')
        },
        handleSizeChange(val) {

        },
        handleCurrentChange(val) {

        },
        /*查询所有新闻*/
        all() {
            this.$axios.post('/tomlive/pressCenter/selectAllPressCenter').then((res) => {
                this.news_list = res.data.info.reverse()
                console.log(this.news_list)
            })
        },
        /*撤回*/
        ret(id) {
            this.$confirm('是否撤回?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/pressCenter/updatePressCenterStatus', this.$qs.stringify({
                        id: id
                    })).then((res) => {
                        console.log(res)
                        // this.$router.push('/home/news_manage')
                        this.all()
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
            console.log(id)
        }
    },
    mounted() {
        /*新闻版块*/
        this.$axios.post('/tomlive/pressType/selectAllPressType').then((res) => {
            var arr = res.data.info;
            var arr2 = [];
            for (var i = 0; i < arr.length; i++) {
                arr2.push(arr[i].pressName)
            }
            // this.options=[...new Set(arr2)]
            this.options = res.data.info
        })
        this.all()
    },
    computed: {
        list() {
            var _this = this
            return this.news_list.filter(function(number) {
                return number.status == 1
            })
        },
        lists() {
            var _this = this
            return this.news_list.filter(function(number) {
                return number.status == 0
            })
        }
    }
}
</script>
<style scoped>
.active {
    color: #0079FE;
    border-bottom: 1px solid #0079FE;
}

.news_mg {
    width: 100%;
    min-height: 800px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
    margin: 20px 0 0 0;
}

.news_title {
    width: 100%;
    height: 80px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
    margin: 20px 0 0 0;
}

.news_t_c {
    width: 100%;
    height: 35px;
    margin: 20px 0 0 0;
    line-height: 35px;
    font-size: 14px;
    color: #666;
}

.news_mg_t {
    width: 100%;
    height: 70px;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.news_mg_c {
    min-height: 50px;
    padding: 20px 30px;
}

.news_mg_c>.el-row {
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
    margin: 10px 0;
    cursor: pointer;
}

.news_mg_c>.n {
    margin: 20px 0;
    border-bottom: none;
}

.news_mg_c_img,
.news_mg_c_img>img {
    width: 100%;
    height: 90px;
}

.tit {
    width: 100%;
    font-size: 16px;
    color: #666;
    font-weight: bold;
    padding: 24px 0 0 0;
}

.tit>span {
    margin: 0px 0 0 20px;
}

.tit_c {
    width: 100%;
    font-size: 14px;
    line-height: 35px;
    color: #666;
}

.tit_c>span {
    color: #999;
    margin: 0 0 0 20px;
}

.tit_r {
    width: 100%;
    height: 90px;
    text-align: center;
    font-size: 12px;
    color: #0000FF;
    line-height: 90px;
}

.tit_rs {
    color: #666;
}

.news_mg_t>button {
    float: right;
    margin: 15px 20px;
}

.news_mg_t>span {
    display: block;
    width: 100px;
    height: 45px;
    text-align: center;
    line-height: 45px;
    font-size: 14px;
    float: left;
    cursor: pointer;
    margin: 24px 0 0 0;
}

.news_mg_t>.ss {
    margin: 24px 0 0 30px;
}

.pages {
    float: right;
}
</style>